import React from 'react';
import "bootstrap-icons/font/bootstrap-icons.css";
import { useHistory, Link } from 'react-router-dom';
import AuthService from "../services/auth.service";
import Toggle from './Toggle';

const Navbar = (props) => {
    let history = useHistory();
    const logOut = () => {
        AuthService.logout().then(
        () => {
            history.push("/home");
            window.location.reload();
        })
    };

    const currentUser = props.user

    return(
        <nav className="navbar navbar-expand-md">
            <div className="container-fluid">
            <Link to={"/home"} className="navbar-brand">
                <i className="bi bi-code-slash" style={{fontSize: "1.8rem"}}></i>
            </Link>
            <button className="navbar-toggler" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <i className="bi-list" style={{fontSize: "1.5rem"}}></i>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="d-flex flex-row align-items-center justify-content-between">
                <div>

                {currentUser ? (
                    <Link className="text-decoration-none" to={"/dashboard"}>
                        {currentUser}
                    </Link>
                ) : (
                    <div className="d-sm-block d-md-none mx-3">Sign in to view dashboard</div>
                )}
                </div>
                <div style={{width: "90px"}}>
                    <Toggle/>
                </div>
            </div>
            {currentUser ? (
                <div className="navbar-nav">
                    <div className="d-grid gap-2 d-md-flex justify-content-md-end">
                        <Link to={"/about"}>
                            <a href="/about" className="btn btn-primary me-md-2 w-100">About Us</a>
                        </Link>
                        <Link to={"/logout"}>
                            <a href="/logout" className="btn btn-primary w-100" onClick={logOut}>Logout</a>
                        </Link>
                    </div>
                </div>
            ) : (
                <div className="navbar-nav">
                    <div className="d-grid gap-2 d-md-flex justify-content-md-end">
                        <Link to={"/about"}>
                            <a href="/about" className="btn btn-primary me-md-2 w-100">About Us</a>
                        </Link>
                        <Link to={"/register"}>
                            <a href="/register" className="btn btn-primary me-md-2 w-100">Register</a>
                        </Link>
                        <Link to={"/login"}>
                            <a href="/login" className="btn btn-primary w-100">Login</a>
                        </Link>
                    </div>
                </div>
            )}
            </div>
            </div>
        </nav>
    )
}

export default Navbar;