import React, { useState } from "react";

import AuthService from "../services/auth.service";

const Login = (props) => {

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");

  const onChangeUsername = (e) => {
    const username = e.target.value;
    setUsername(username);
  };

  const onChangePassword = (e) => {
    const password = e.target.value;
    setPassword(password);
  };

  const handleLogin = (e) => {
    e.preventDefault();
    AuthService.login(username, password).then(
      () => {
        setMessage(null)
        props.history.push("/dashboard");
        window.location.reload();
      }).catch(
        (error) => {
          console.log(error);
          const resMessage = error.response.data.message
          console.log(error.response.data)
          setMessage(resMessage)
        }
      )
  };

  return (
    <main>
      <h1 className="display-1">
        Login
      </h1>
      <div className="form-container row-cols-lg-auto mt-5 pt-3 align-items-center">
        <form onSubmit={handleLogin}>
          <div className="container">
            <div className="row g-3">
              <div className="col-12">
                <div className="form-group">
                  <input type="text" name="username" className="form-control" id="inputUsername" value={username} onChange={onChangeUsername} required />
                  <label className="form-control-placeholder" htmlFor="name">Username</label>
                </div>
              </div>
              <div className="col-12">
                <div className="form-group">
                  <input type="password" name="password" className="form-control" id="inputPassword" required value={password} onChange={onChangePassword}/>
                  <label className="form-control-placeholder" htmlFor="sludge">Password</label>
                </div>
              </div>
              <div className="col-12">
                <button type="submit" className="btn btn-primary btn-block w-100">Login</button>
              </div>
            </div>
            {message && (
            <div className="col-12 text-center">
            <div className="form-group">
              <div className="alert alert-danger" role="alert">
                {message}
              </div>
            </div>
            </div>
          )}
          </div>
        </form>
      </div>
    </main>
  );
};

export default Login;