import React, { useState } from 'react';
import "bootstrap-icons/font/bootstrap-icons.css";
import AuthService from "../services/auth.service";

const Home = () => {

    const [url, setUrl] = useState("");
    const [sludge, setSludge] = useState("");
    const [shortUrl, setShortUrl] = useState(null);
    const [error, setError] = useState(null);

    const onChangeUrl = (e) => {
        const url = e.target.value;
        setUrl(url);
    };

    const onChangeSludge = (e) => {
        const sludge = e.target.value;
        setSludge(sludge);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        AuthService.getShortUrl(url, sludge).then(
          (res) => {
              setError(null)
              console.log(res)
              setShortUrl(res.shortUrl)
          }).catch(
            (error) => {
                console.log(error)
                const resMessage = error.response.data.message || ""
                console.log(error.response.data)
                setShortUrl(null)
                setError(resMessage)
              }
          )
    };

    return(
        <main>
            <div className="title">
                <h1 className="display-1">
                    Shorten.Codes
                </h1>
                <p className="text-end fw-light fs-6">say NO to long URLs</p>
            </div>
            {/* Say no to long url */}
            <div className="form-container col-lg-8 mt-5 pt-3">
                <form onSubmit={handleSubmit}>
                    <div className="container">
                        <div className="row g-3">
                            <div className="col-md-6 col-lg-5">
                                <div className="form-group">
                                <input type="text" name="url" id="url" className="form-control" value={url} onChange={onChangeUrl} required />
                                <label className="form-control-placeholder" htmlFor="name">URL</label>
                                </div>
                            </div>
                            <div className="col-md-6 col-lg-5">
                                <div className="form-group">
                                <input type="text" name="sludge" id="sludge" className="form-control" value={sludge} onChange={onChangeSludge} required />
                                <label className="form-control-placeholder" htmlFor="sludge">Nickname</label>
                                </div>
                            </div>
                            <div className="col-lg-2 col-sm-12">
                                <button type="submit" className="btn btn-primary btn-block w-100">Generate</button>
                            </div>
                            
                            <div className="col-12 text-center">
                                <div className="form-group">
                                    {shortUrl && (
                                    <div className="alert alert-success" role="alert">
                                        <a href={shortUrl} className="text-center" target="_blank" rel="noreferrer">{shortUrl}</a>
                                    </div>
                                    )}
                                    {error && (
                                    <div className="alert alert-danger" role="alert">
                                        {error}
                                    </div>
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </form>
            </div>

            <blockquote class="blockquote text-center">
                <p className="fw-light"><small>We recommend registering an account with us in order save your shortened URLs and see the associated analytics.</small></p>
            </blockquote>
        </main>
    )
}

export default Home;