import React, { useState, useEffect } from 'react';
import Table from './Table';


import AuthService from "../services/auth.service";

const Dashboard = () => {

    const [data, setData] = useState([]);

    useEffect(() => {
        AuthService.getLinks().then(
            (response) => {
                console.log(response)
                setData(response.links)
            }
        )
      }, []);

      const getdata = () => {
        AuthService.getLinks().then(
            (response) => {
                setData(response.links)
            }
        )
    }

      const columns = React.useMemo(
        () => [
          {
            Header: 'Nickname',
            accessor: 'urlCode',
          },
          {
            Header: 'ShortUrl',
            accessor: 'shortUrl',
            Cell: e =><a className="text-decoration-none" href={e.value} target="_blank" rel="noreferrer" onClick={getdata}> {e.value} </a>
          },
          {
            Header: 'longUrl',
            accessor: 'longUrl',
            Cell: e =><a className="text-decoration-none" href={e.value} target="_blank" rel="noreferrer"> {e.value} </a>
          },
          {
            Header: 'clicks',
            accessor: 'clicks'
          },
        ],
        []
      )

    return(
        <div>
      <h1 className="display-1">
        Dashboard
      </h1>
      <hr/>
            {data &&
                <div className="table-responsive mb-5">
                    <Table columns={columns} data={data} />
                </div>
            }
        </div>
    )
}

export default Dashboard;